<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="120px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="运动名称" prop="yundongmingcheng" >
					<el-input v-model="ruleForm.yundongmingcheng" placeholder="运动名称" clearable  :readonly="ro.yundongmingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动名称" prop="yundongmingcheng" >
					<el-input v-model="ruleForm.yundongmingcheng" placeholder="运动名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="运动类型" prop="yundongleixing" >
					<el-input v-model="ruleForm.yundongleixing" placeholder="运动类型" clearable  :readonly="ro.yundongleixing"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动类型" prop="yundongleixing" >
					<el-input v-model="ruleForm.yundongleixing" placeholder="运动类型" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.tupian" label="图片" prop="tupian" >
					<file-upload
						tip="点击上传图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.tupian?ruleForm.tupian:''"
						@change="tupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.tupian" label="图片" prop="tupian" >
					<img v-if="ruleForm.tupian.substring(0,4)=='http'&&ruleForm.tupian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian" width="100" height="100">
					<img v-else-if="ruleForm.tupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.tupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.tupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info'&& !ro.shipin" label="视频" prop="shipin" >
					<file-upload
						tip="点击上传视频"
						action="file/upload"
						:limit="1"
						:type="2"
						:multiple="true"
						:fileUrls="ruleForm.shipin?ruleForm.shipin:''"
						@change="shipinUploadChange"
					></file-upload>
				</el-form-item> 
				<el-form-item v-else-if="ruleForm.shipin" label="视频" prop="shipin" >
					<el-button class="viewBtn" type="text" size="small" @click="download($base.url+ruleForm.shipin)">
						<span class="icon iconfont icon-chakan14"></span>
						预览
					</el-button>
				</el-form-item>
				<el-form-item v-else-if="!ruleForm.shipin" label="视频" prop="shipin" >
					<el-button class="unBtn" type="text" size="small">
						<span class="icon iconfont icon-xihuan"></span>
						暂无
					</el-button>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="运动强度" prop="yundongqiangdu" >
					<el-input v-model="ruleForm.yundongqiangdu" placeholder="运动强度" clearable  :readonly="ro.yundongqiangdu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动强度" prop="yundongqiangdu" >
					<el-input v-model="ruleForm.yundongqiangdu" placeholder="运动强度" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="运动周期" prop="yundongzhouqi" >
					<el-input v-model="ruleForm.yundongzhouqi" placeholder="运动周期" clearable  :readonly="ro.yundongzhouqi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动周期" prop="yundongzhouqi" >
					<el-input v-model="ruleForm.yundongzhouqi" placeholder="运动周期" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="运动备注" prop="yundongbeizhu" >
					<el-input v-model="ruleForm.yundongbeizhu" placeholder="运动备注" clearable  :readonly="ro.yundongbeizhu"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动备注" prop="yundongbeizhu" >
					<el-input v-model="ruleForm.yundongbeizhu" placeholder="运动备注" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="运动目标" prop="yundongmubiao" >
					<el-input v-model="ruleForm.yundongmubiao" placeholder="运动目标" clearable  :readonly="ro.yundongmubiao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="运动目标" prop="yundongmubiao" >
					<el-input v-model="ruleForm.yundongmubiao" placeholder="运动目标" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="用户账号" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" clearable  :readonly="ro.yonghuzhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="用户账号" prop="yonghuzhanghao" >
					<el-input v-model="ruleForm.yonghuzhanghao" placeholder="用户账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="用户姓名" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" clearable  :readonly="ro.yonghuxingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="用户姓名" prop="yonghuxingming" >
					<el-input v-model="ruleForm.yonghuxingming" placeholder="用户姓名" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	export default {
		data() {
			return {
				id: '',
				type: '',
			
			
				ro:{
					yundongmingcheng : false,
					yundongleixing : false,
					tupian : false,
					shipin : false,
					yundongqiangdu : false,
					yundongzhouqi : false,
					yundongbeizhu : false,
					yundongmubiao : false,
					yonghuzhanghao : false,
					yonghuxingming : false,
				},
			
				ruleForm: {
					yundongmingcheng: '',
					yundongleixing: '',
					tupian: '',
					shipin: '',
					yundongqiangdu: '',
					yundongzhouqi: '',
					yundongbeizhu: '',
					yundongmubiao: '',
					yonghuzhanghao: '',
					yonghuxingming: '',
				},

				rules: {
					yundongmingcheng: [
						{ required: true, message: '运动名称不能为空', trigger: 'blur' },
					],
					yundongleixing: [
						{ required: true, message: '运动类型不能为空', trigger: 'blur' },
					],
					tupian: [
					],
					shipin: [
					],
					yundongqiangdu: [
					],
					yundongzhouqi: [
					],
					yundongbeizhu: [
					],
					yundongmubiao: [
					],
					yonghuzhanghao: [
					],
					yonghuxingming: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					for(let x in this.ro) {
						this.ro[x] = true
					}
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='yundongmingcheng'){
							this.ruleForm.yundongmingcheng = obj[o];
							this.ro.yundongmingcheng = true;
							continue;
						}
						if(o=='yundongleixing'){
							this.ruleForm.yundongleixing = obj[o];
							this.ro.yundongleixing = true;
							continue;
						}
						if(o=='tupian'){
							this.ruleForm.tupian = obj[o];
							this.ro.tupian = true;
							continue;
						}
						if(o=='shipin'){
							this.ruleForm.shipin = obj[o];
							this.ro.shipin = true;
							continue;
						}
						if(o=='yundongqiangdu'){
							this.ruleForm.yundongqiangdu = obj[o];
							this.ro.yundongqiangdu = true;
							continue;
						}
						if(o=='yundongzhouqi'){
							this.ruleForm.yundongzhouqi = obj[o];
							this.ro.yundongzhouqi = true;
							continue;
						}
						if(o=='yundongbeizhu'){
							this.ruleForm.yundongbeizhu = obj[o];
							this.ro.yundongbeizhu = true;
							continue;
						}
						if(o=='yundongmubiao'){
							this.ruleForm.yundongmubiao = obj[o];
							this.ro.yundongmubiao = true;
							continue;
						}
						if(o=='yonghuzhanghao'){
							this.ruleForm.yonghuzhanghao = obj[o];
							this.ro.yonghuzhanghao = true;
							continue;
						}
						if(o=='yonghuxingming'){
							this.ruleForm.yonghuxingming = obj[o];
							this.ro.yonghuxingming = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.yonghuzhanghao = json.yonghuzhanghao
							this.ro.yonghuzhanghao = true;
						}
						if(((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.yonghuxingming = json.yonghuxingming
							this.ro.yonghuxingming = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `yundongjihua/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					if(this.ruleForm.tupian!=null) {
						this.ruleForm.tupian = this.ruleForm.tupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					if(this.ruleForm.shipin!=null) {
						this.ruleForm.shipin = this.ruleForm.shipin.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `yundongjihua/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.yundongjihuaCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.yundongjihuaCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			tupianUploadChange(fileUrls) {
				this.ruleForm.tupian = fileUrls;
			},
			shipinUploadChange(fileUrls) {
				this.ruleForm.shipin = fileUrls;
			},
		}
	};
</script>
